.footer {
	margin-top: 92px;
	background-image: url('../img/bg-footer.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	padding: 118px 0 33px;

	@media (max-width: 991px) {
		padding-top: 50px;
	}

	&__body {
		position: relative;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__bottom {
		margin-top: 85px;
	}

	&__box {
		@include jcsb;

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 20px;
		}

	}

	&__info {
		margin-top: 60px;
		@include jcsb;

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 20px;
			text-align: center;
		}
	}

	&__copy {
		font-size: 13px;
		color: #777;
		line-height: calc(20 / 13);
	}

	&__link {
		font-size: 13px;
		color: #777;

		&:hover {
			text-decoration: underline;
		}
	}
}

.img-footer {
	@media (max-width: 991px) {
		text-align: center;
		margin-bottom: 50px;
	}

	&__item {
		&--1 {
			@media (min-width: 1225px) {
				margin-left: -70px;
			}

			@media (max-width: 1145px) {
				width: 500px;
			}
		}

		&--2 {
			position: absolute;
			top: 0;
			left: 130px;
			animation: robot 2.5s ease infinite;

			@media (max-width: 1224px) {
				left: 230px;
			}

			@media (max-width: 1145px) {
				left: 180px;
			}

			@media (max-width: 991px) {
				left: 41%;
			}

			@media (max-width: 575px) {
				left: 33%;
			}

			@media (max-width: 380px) {
				width: 80px;
				left: 35%;
			}
		}
	}
}

@keyframes robot {
	0% {
		transform: translateY(0)
	}

	50% {
		transform: translateY(25px)
	}
}