.header {
	position: absolute;
	top: -150px;
	left: 0;
	width: 100%;
	transition: top 1.5s ease;

	&__content {
		display: flex;
		align-items: center;

		@media (max-width: 1145px) {
			justify-content: space-between;
		}
	}

	&__tel {
		position: relative;
		padding-left: 30px;
		font-weight: 700;
		font-size: 13px;
		line-height: 16px;
		color: #494949;
		text-transform: uppercase;
		letter-spacing: 0.105em;

		@media (min-width: 1146px) {
			margin-left: auto;
		}

		@media (max-width: 991px) {
			width: max-content;
			margin: 40px auto 0;
		}

		&:hover {
			opacity: 0.7;
			color: inherit;

		}

		&::before {
			content: '';
			background-image: url('../img/svg/call.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 0px;
			left: 0px;
			width: 18px;
			height: 18px;
		}
	}

	&__social {
		display: none;

		@media (max-width: 991px) {
			display: flex;
			justify-content: center;
			margin-top: 40px;
		}
	}
}

.loaded .header {
	top: 45px;
}

.menu {

	@media (min-width: 1146px) {
		margin-left: 159px;
	}

	&__body {
		@media (max-width: 991px) {
			background-color: #fff;
			position: fixed;
			display: flex;
			flex-direction: column;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			overflow: auto;
			padding: 130px 20px 20px;
			opacity: 0;
			visibility: hidden;
			transition: all .3s ease 0s;
			text-align: center;
			z-index: 2;

			&:before {
				content: "";
				background-color: #fff;
				position: fixed;
				top: 0;
				left: 0;
				width: 100%;
				height: 106px;
				z-index: 50;
			}

			&.active {
				opacity: 1;
				visibility: visible;
			}
		}

		&.active {
			@media (max-width: 991px) {
				.menu__list {
					opacity: 1;
					visibility: visible;
				}
			}
		}
	}

	&__list {
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}

		&>li {
			@media (max-width: 991px) {
				border-bottom: 1px solid #ccc;

				&:first-child {
					border-top: 1px solid #ccc;
				}
			}
		}

		&>li+li {
			margin-left: 51px;

			@media (max-width: 991px) {
				margin-left: 0;
			}
		}
	}

	&__link {
		font-size: 15px;
		color: #494949;

		@media (max-width: 991px) {
			display: block;
			padding: 20px 0;
		}
	}
}

@media (max-width: 991px) {
	.menu__icon {
		z-index: 5;
		display: block;
		position: relative;
		width: 35px;
		height: 20px;
		cursor: pointer;
	}

	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 10%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: #44B875;
		border-radius: 10px;
	}

	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}

	.menu__icon::before {
		top: 0;
	}

	.menu__icon::after {
		bottom: 0;
	}

	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon.active span {
		transform: scale(0) translate(0px, -50%);
	}

	.menu__icon.active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
	}

	.menu__icon.active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
	}

	.menu__icon i {
		content: "";
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		z-index: -1;
		width: 50px;
		height: 50px;
		background: transparent;
		border-radius: 7px;
		opacity: 0.5;
		transition: 0.3s ease-out;
	}

