.intro {
	padding: 178px 0 147px;
	background-image: url('../img/bg-intro.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 991px) {
		padding: 120px 0;
	}

	@media (max-width: 575px) {
		padding-bottom: 60px;
	}

	&__content {
		position: relative;
		display: flex;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {
		flex: 0 0 60%;

		@media (max-width: 1370px) {
			flex: 0 0 50%;
		}

		@media (max-width: 991px) {
			text-align: center;
		}
	}

	&__title {
		font-family: 'Nunito', sans-serif;
		font-weight: 700;
		font-size: 62px;
		line-height: calc(79 / 62);

		@media (max-width: 1370px) {
			font-size: 48px;
		}

		@media (max-width: 1145px) {
			font-size: 38px;
		}

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__desc {
		margin: 15px 0 47px;
		font-size: 28px;
		line-height: calc(37 / 28);

		@media (max-width: 1370px) {
			font-size: 24px;
		}

		@media (max-width: 1145px) {
			font-size: 18px;
			margin-bottom: 20px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__btn {
		@media (max-width: 991px) {
			display: flex;
			justify-content: center;
			margin-top: 30px;
		}
	}
}

.img-intro {
	@media (max-width: 991px) {
		order: -1;
		margin: 0 auto 50px;
	}

	@media (max-width: 575px) {
		margin-bottom: 30px;
	}

	&--1 {
		position: absolute;
		right: -155px;
		top: -41px;
		width: 658px;
		height: 544px;
		object-fit: cover;

		@media (max-width: 1370px) {
			right: 0;
			width: 558px;
			height: 464px;
		}

		@media (max-width: 1145px) {
			width: 458px;
			height: 380px;
		}

		@media (max-width: 991px) {
			position: initial;
		}

		@media (max-width: 575px) {
			width: 300px;
			height: 243px;
		}
	}

	&--2 {
		position: absolute;
		top: -48px;
		right: 96px;
		width: 279px;
		height: 383px;
		object-fit: cover;
		animation: robot 2.5s ease infinite;

		@media (max-width: 1370px) {
			top: -48px;
			right: 206px;
			width: 229px;
			height: 313px;
		}

		@media (max-width: 1145px) {
			top: -63px;
			right: 166px;
			width: 196px;
			height: 270px;
		}

		@media (max-width: 991px) {
			top: -21px;
			right: 42%;
		}

		@media (max-width: 575px) {
			top: -24px;
			right: 40%;
			width: 126px;
			height: 170px;
		}
	}
}