html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320px;
	font-family: "Montserrat", sans-serif;
	font-weight: 400;
	color: #1E1E1E;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

a {
	text-decoration: none;
	color: #1E1E1E;
	transition: all 0.3s ease;

	&:hover {
		color: #44B875;
	}
}

ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1145px;

	@media (max-width: 1145px) {
		max-width: 970px;
	}

	@media (max-width: 991px) {
		max-width: 750px;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

.title {
	font-family: 'Nunito', sans-serif;
	font-weight: 700;
	font-size: 42px;
	text-align: center;
	color: #1E1E1E;

	@media (max-width: 991px) {
		font-size: 34px;
	}

	@media (max-width: 575px) {
		font-size: 22px;
	}
}