.portfolio {
	padding-top: 148px;

	@media (max-width: 767px) {
		padding-top: 60px;
	}

	&__swiper {
		margin-top: 50px;

		@media (max-width: 575px) {
			padding-bottom: 70px;
		}
	}

	// .swiper {
	// 	width: 100%;
	// 	height: 100%;
	// }

	.swiper-slide {
		@media (min-width: 576px) {
			width: 60%;
			text-align: center;
		}
	}

	.portfolio__img {
		@media (max-width: 575px) {
			margin: 0 15px;
		}
	}

	.swiper-pagination-bullet {
		width: 12px;
		height: 12px;
	}

	.swiper-pagination-bullet-active {
		background-color: #44B875;
	}
}