.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 10;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		max-width: 540px;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70px);
		border-radius: 39px;
		padding: 60px 0;

		@media (max-width: 767px) {
			margin: 0 15px;
			padding: 30px 0;
		}

		@media (max-width: 575px) {
			border-radius: 15px;
		}
	}

	&__close {
		position: absolute;
		right: 20px;
		top: 15px;
		font-size: 20px;
		color: #000;
		text-decoration: none;
		z-index: 3;

		img {
			width: 30px;
			height: 30px;
		}
	}
}