.advantage {
	padding-top: 113px;

	@media (max-width: 767px) {
		padding-top: 55px;
	}

	&__content {
		margin-top: 80px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-wrap: wrap;
			gap: 50px;
		}

		@media (max-width: 575px) {
			margin-top: 40px;
		}
	}

	&__item {
		flex: 0 0 30%;

		@media (max-width: 991px) {
			flex: 0 0 45%;
		}

		@media (max-width: 575px) {
			flex: 0 0 100%;
		}
	}

	&__subtitle {
		font-family: 'Nunito', sans-serif;
		margin-top: 24px;
		font-size: 22px;

		@media (max-width: 575px) {
			font-size: 18px;
			margin-top: 15px;
		}
	}

	&__text {
		margin-top: 12px;
		font-size: 15px;
		line-height: calc(24 / 15);

		@media (max-width: 575px) {
			font-size: 13px;
		}
	}
}