.btn {
	position: relative;
	overflow: hidden;
	display: flex;
	align-items: center;
	justify-content: center;
	text-transform: uppercase;
	width: 300px;
	height: 80px;
	color: #fff;
	border-radius: 8px;
	border: 2px solid transparent;
	transition: all 0.3s ease-out;
	font-weight: 700;
	background-color: #44B875;
	transform: perspective(1px) translateZ(0);
	text-align: center;
	letter-spacing: 0.105em;

	&:hover {
		background-color: #33955B;
		color: #fff;
	}

	@media (max-width: 575px) {
		font-size: 14px;
		padding: 20px;
		text-align: center;
		width: 100%;
	}

	&--animate {
		height: 250px;
		width: 250px;
		position: absolute;
		top: -20px;
		left: -120px;
		background: rgba(255, 255, 255, 1.0);
		border-radius: 100%;
		transform: scale(0);
		animation: ripple 4s linear infinite;
	}

	@keyframes ripple {
		20% {
			opacity: 0;
			transform: scale(2.5);
		}

		100% {
			opacity: 0;
			transform: scale(2.5);
		}
	}

	&:disabled {
		opacity: 0.5;
		cursor: default;
		color: #fff;

		&:before,
		&:hover:before {
			background-color: #44B875;
		}
	}
}

.btn+.btn {
	margin-left: 15px;

	@media (max-width: 991px) {
		margin: 0;
	}
}