.fits {
	margin-top: 140px;
	padding: 96px 0 118px;
	background-image: url('../img/bg-fits.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 767px) {
		margin-top: 55px;
		padding: 55px 0;
	}

	&__content {
		display: flex;
		gap: 135px;
		margin-top: 60px;

		@media (max-width: 767px) {
			flex-direction: column;
			gap: 45px;
			margin-top: 30px;
		}
	}

	&__subtitle {
		font-family: 'Nunito', sans-serif;
		font-size: 22px;
		font-weight: 400;

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__list {
		margin-top: 32px;
	}

	&__item {
		position: relative;
		padding-left: 45px;
		line-height: calc(22 /16);

		@media (max-width: 575px) {
			font-size: 14px;
		}

		&::before {
			content: '';
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 50%;
			left: 0;
			transform: translateY(-50%);
			width: 32px;
			height: 32px;
		}

		&--1 {
			&::before {
				background-image: url('../img/svg/check-1.svg');
			}
		}

		&--2 {
			&::before {
				background-image: url('../img/svg/check-2.svg');
			}
		}
	}

	&__item+&__item {
		margin-top: 30px;
	}

	&__calculate {
		margin-top: 50px;
		@include jcsb;
		padding: 40px;
		background: #fff;
		border-radius: 26px;

		@media (max-width: 991px) {
			flex-direction: column;
			gap: 30px;
			text-align: center;
			padding: 40px 20px;
		}
	}

	&__text {
		max-width: 670px;
		font-size: 19px;
		line-height: calc(29 / 19);

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}
}