.price {
	margin-top: 113px;
	padding: 92px 0;
	background-image: url('../img/bg-price.png');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;

	@media (max-width: 767px) {
		margin-top: 55px;
		padding: 55px 0;
	}

	&__content {
		margin-top: 66px;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 25px;


		@media (max-width: 1145px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 767px) {
			grid-template-columns: 1fr;
		}

		@media (max-width: 575px) {
			margin-top: 40px;
		}
	}

	&__item {
		display: flex;
		flex-direction: column;
		flex: 0 0 32%;
		background-color: #fff;
		border-radius: 30px;
		padding: 68px 28px 50px;

		@media (max-width: 1145px) {
			text-align: center;
		}

		@media (max-width: 575px) {
			padding: 40px 30px;
		}
	}

	&__cost {
		font-family: 'Nunito', sans-serif;
		font-weight: 700;
		font-size: 36px;
		text-align: center;

		@media (max-width: 575px) {
			font-size: 24px;
		}
	}

	&__subtitle {
		margin-top: 55px;
		font-family: 'Nunito', sans-serif;
		font-weight: 400;
		font-size: 22px;
		text-align: center;

		@media (max-width: 575px) {
			margin-top: 30px;
			font-size: 18px;
		}
	}

	&__text {
		margin: 20px 0 44px;
		min-height: 112px;
		font-size: 15px;
		line-height: calc(23 / 15);

		@media (max-width: 575px) {
			font-size: 13px;
		}

		p+p {
			margin-top: 10px;
		}
	}

	&__btn {
		margin-top: auto;

		@media (max-width: 1145px) {
			margin-left: auto;
			margin-right: auto;
		}
	}
}