.form {
	flex: 0 0 40%;
	background-color: #fff;
	padding: 34px 44px;
	border-radius: 30px;

	@media (max-width: 1145px) {
		padding: 25px 20px;
	}

	&__title {
		font-family: 'Nunito', sans-serif;
		font-weight: 700;
		font-size: 36px;
		text-align: center;

		@media (max-width: 1145px) {
			font-size: 24px;
		}
	}

	&__desc {
		margin-top: 20px;
		font-size: 15px;
		line-height: calc(22 / 15);
		text-align: center;

		@media (max-width: 1145px) {
			font-size: 13px;
		}
	}

	&__group {
		margin-top: 25px;

		input {
			border-bottom: 1px solid #E2E2E2;
			width: 100%;
			padding: 30px 0 12px;
			outline: none;
			color: #ABABAB;

			@media (max-width: 575px) {
				font-size: 14px;
			}

			&::placeholder {
				color: #ABABAB;
			}
		}
	}

	&__btn {
		margin-top: 30px;
		width: 100%;
	}

	&__label {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		gap: 10px;
		margin-top: 30px;
		font-size: 11px;
		cursor: pointer;

		input {
			display: none
		}

		input:checked+span {
			background-image: url('../img/svg/check.svg');
		}

		span {
			width: 100%;
			max-width: 22px;
			height: 22px;
			background: #EAEAEA;
			border-radius: 4px;
			background-size: 20px;
			background-position: center;
			background-repeat: no-repeat;
		}

		i {
			display: inline-block;
			font-style: normal;
		}

		a {
			color: #44B875;

			&:hover {
				text-decoration: underline;
			}
		}
	}
}