.faq {
	padding-top: 88px;

	@media (max-width: 767px) {
		padding-top: 60px;
	}

	&__content {
		margin-top: 54px;
	}

	&__item {
		border-top: 1px solid #EDEDED;
		border-bottom: 1px solid #EDEDED;
	}

	&__title {
		position: relative;
		font-family: 'Nunito', sans-serif;
		font-size: 22px;
		padding: 33px 0;
		cursor: pointer;

		@media (max-width: 767px) {
			font-size: 16px;
			padding-right: 40px;
		}

		&::before {
			content: '';
			background-image: url('../img/svg/plus.svg');
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			position: absolute;
			top: 30px;
			right: 5px;
			width: 34px;
			height: 34px;
			transition: all 0.3s ease;

			@media (max-width: 767px) {
				width: 20px;
				height: 20px;
			}
		}

		&.active {
			&:before {
				transform: rotate(45deg);
			}
		}
	}

	&__body {
		display: none;
		font-size: 15px;
		line-height: calc(24 / 15);
		padding-bottom: 20px;

		@media (max-width: 767px) {
			font-size: 14px;
		}
	}
}